import { 
  VStack,
  FormLabel,
  FormControl,
  Input,
  InputGroup, 
  Button,
  InputRightElement,
  useToast} from '@chakra-ui/react';

import React, { useState } from 'react';
import axios from 'axios';
import {useHistory} from 'react-router-dom';

const Signup = () => {
  const [show, setshow] = useState(false)
  const [name, setName] = useState();
  const [email, setEmail] = useState();
  const [password, setPassword] = useState();
  const [confirmpassword, setConfirmpassword] = useState();
  const [pic, setPic] = useState();
  const [ loading, setLoading] = useState(false)
  const toast = useToast();
  const history = useHistory();

  const handleClick = () => {
     setshow(!show);
  }
  const postDetailes = (pics) => { 
     setLoading(true);
     if (pics === undefined){
              toast({
                title: 'Account created.',
                // description: "We've created your account for you.",
                status: 'warning',
                duration: 5000,
                isClosable:true,
                position:"bottom",
              });
              return;
            }
      // console.log(pics);
      if( pics.type === "image/jpeg" || pics.type === "image/png" ){
         const data = new FormData();
         data.append("file", pics);
         data.append("upload_preset", "Mern-Chat-app");
         data.append("cloud_name", "djjgkngwv");
         fetch("https://api.cloudinary.com/v1_1/djjgkngwv/image/upload",{
            method:'POST',
            body:data,
         }).then((res) => res.json())
         
         .then((data) => {
            console.log(data); 
            // console.log(data.url); 
            setPic(data.url.toString());
            setLoading(false);
         })
         .catch((err) => {
          console.log(err);
          setLoading(false);
         });
      } else {
        toast({
          title: 'Account created.',
          status: 'warning',
          duration: 5000,
          isClosable:true,
          position:"bottom",
        });
        setLoading(false);
        return;
      }

      };
     
  const submitHandler = async() => {
     setLoading(true); 

    if(!name || !email || !password || !confirmpassword){
      toast({
        title:"Please Fill all the Feilds",
        status:"warning",
        duration: 5000,
        isClosable: true,
        position: "bottom",
      });
      setLoading(false);
      return;
    }

    if( password !== confirmpassword){
      toast({
        title:"Please Fill all the Feilds",
        status:"warning",
        duration: 5000,
        isClosable: true,
        position: "bottom",
      });
      setLoading(false);
      return;
    }

    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };

      const { data } = await axios.post(
        " /api/user/",
        {  name, email, password, pic }, 
        config 
      );
      toast({
        title: 'Registration Successful',
        status: 'success',
        duration: 5000,
        isClosable:true,
        position:"bottom",
      });
      
      localStorage.setItem("userInfo", JSON.stringify(data));

      setLoading(false);
      history.push("/chats");
    } catch (error) {
      toast({
        title: 'Registration Error Ocurred!',
        description:error.response.data.message,
        status: 'error',
        duration: 5000,
        isClosable:true,
        position:"bottom",
      });
      setLoading(false);
     }


  };

  return (
    <VStack spacing="5px" color='black'>
        <FormControl id='first-name' isRequired>
           <FormLabel>Name</FormLabel>
           <Input 
             placeholder ="Enter Your Name"
             onChange ={(e) => setName(e.target.value)}
           />
        </FormControl>

        <FormControl id='s-email' isRequired>
           <FormLabel>Email</FormLabel>
           <Input 
             placeholder ="Enter Your Email"
             onChange ={(e) => setEmail(e.target.value)}
           />
        </FormControl>

        <FormControl id='s-password' isRequired>
          <FormLabel>Password</FormLabel>
          <InputGroup>
            <Input 
              type={ show ? 'text' :'password'}
              placeholder ="Enter Your Password"
              onChange ={(e) => setPassword(e.target.value)}
            />
            <InputRightElement width='4.5rem'>
              <Button h='1.75rem' size='sm' onClick={handleClick}>
                  { show ? "Hide" : "show" }
              </Button>
            </InputRightElement>
          </InputGroup>
        </FormControl>

        <FormControl id='confirmpassword' isRequired>
          <FormLabel>Confirm Password</FormLabel>
          <InputGroup>
            <Input 
              type={ show ? 'text' :'password'}
              placeholder ="Confirm Password"
              onChange ={(e) => setConfirmpassword(e.target.value)}
            />
            <InputRightElement width='4.5rem'>
              <Button h='1.75rem' size='sm' onClick={handleClick}>
                  { show ? "Hide" : "show" }
              </Button>
            </InputRightElement>
          </InputGroup>
        </FormControl>

        <FormControl id='pic'>
           <FormLabel>Upload your Picture</FormLabel>
           <Input 
             type='file'
             p={1.5}
             accept='image/*' 
             onChange ={(e) => postDetailes(e.target.files[0])}
           />
        </FormControl>

        <Button
         colorScheme='blue'
         width='100%'
         style={{ marginTop: 15}}
         onClick={submitHandler}
         isLoading = { loading }
        >
          Sign Up
        </Button>
    </VStack>
  );
};

export default Signup;